input {
    text-align: center;
    font-size: 40px;
    color: rgb(70, 70, 70);
    background-color: none;
}

::placeholder {
    font-size: 40px;
    text-align: center;
    color: rgb(120, 120, 120);
}

textarea:focus, input:focus {
    outline: none;
}

.search-filter {
    padding-bottom: 50px;
}

.search-bar {
    width: 100%;
    height: 100px;
    border: none;
    border-bottom: 2px solid green;
    background-color: #F8F8F8;
    margin-bottom: 10px;
}

.col-filter {
    padding-left: 0px;
    padding-right: 0px;
}

.park-card {
    box-shadow: 0 15px 35px rgba(50,50,93,.1), 0 5px 15px rgba(0,0,0,.07);
}

.card-img-top {
    object-fit: cover;
    overflow: hidden;
    width: 100%;
    height: 300px;
}

.card-body {
    height: 200px;
}

.card-title {
    font-weight: bold;
    color: rgb(70, 70, 70);
}

.list-parks {
    display: grid;
    grid-template-columns: repeat(3, 3fr);
    grid-auto-rows: auto;
    grid-gap: 3rem;
}